import './App.css';
import {useState , UseEffect, useEffect } from 'react';


function App() {
const [preState,setPrestate] = useState("");
const [curState , setCurstate] = useState("");
const [input , setInput] = useState("");
const [operator , setOperator] = useState(null);
const [total , setTotal] = useState(false);

const operatorType=(e)=>{
  setInput(input.concat(e.target.id));
}

 

const inputnum=(e)=>{
  setInput(input.concat(e.target.id));

}
const clear=()=>{
  setInput("");

};
const plusminus=()=>{

  setInput(-(input))

};

const equal=(e)=>{
  setInput(eval(input).toString());

};
  return (
    <div className='container'>
      <div className='wrapper'>
        <div className='screen'>{input}</div>
        <div className='btn lightgray' onClick={clear}>Ac</div>
        <div  id="%" className='btn lightgray' onClick={operatorType}>%</div>
        <div className='btn lightgray' onClick={plusminus}>+/-</div>
        <div id="/" className='btn operator' onClick={operatorType}>/</div>
        <div id="7" className='btn' onClick={inputnum}>7</div>
        <div id="8" className='btn' onClick={inputnum}>8</div>
        <div id="9" className='btn' onClick={inputnum}>9</div>
        <div id="*" className='btn operator' onClick={operatorType}>X</div>
        <div id="4" className='btn' onClick={inputnum}>4</div>
        <div id="5" className='btn' onClick={inputnum}>5</div>
        <div id="6" className='btn' onClick={inputnum}>6</div>
        <div id="+" className='btn operator'onClick={operatorType}>+</div>
        <div id="1" className='btn' onClick={inputnum}>1</div>
        <div id="2" className='btn' onClick={inputnum}>2</div>   
        <div id="3" className='btn' onClick={inputnum}>3</div>
        <div id="-" className='btn operator' onClick={operatorType}>-</div>
        <div id="0" className='btn zero' onClick={inputnum}>0</div>
        <div id="." className='btn' onClick={inputnum}>.</div>
        <div className='btn operator' onClick={equal}>=</div>



        </div>
    </div>
  );
}

export default App;

